.contact {


  .container {
    p {
      text-align: center;
    }
  }

  &__content {
    display: flex;
    margin-top: 70px;

  }

  &__form {
    max-width: 730px;
    flex-basis: 730px;
    flex-grow: 1;
  }

  &__info {
    flex-grow: 1;
    padding-right: 30px;
    font-size: 12px;
    line-height: 20px;

    a {
      font-size: 12px;
      color: $font-color;
      transition: .3s;

      &:hover {
        text-decoration: underline;
      }
    }

    &-item {
      position: relative;
      padding-left: 30px;

      &+.contact__info-item {
        margin-top: 28px;
      }

      img {
        position: absolute;
        left: 0;
        top: 0;
        max-width: 15px;
      }

      &:last-child {

        img {
          top: 4px;
        }
      }
    }
  }
}